html, body {
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
}

html {
  //overflow-y: scroll;
  //min-width: 980px;
  //min-height: 768px;
  background-color: #ebebeb;
}

body {
  display: block;
  position: relative;
}

html, body, div, header,
nav, aside, article, section,
footer, p, span, h1,
h2, h3, h4, h5, h6,
figure, figcaption, fieldset,
input, button, ul, li,
label {
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  vertical-align: baseline;
  font-size: 100%;
  font-weight: 400;
}

a, a:visited, a:active {
  text-decoration: none;
  outline: none;
  color: inherit;
}

article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  display: block;
}

#main_header {
  width: 100%;
  height: 95px;
  background-color: #fff;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: normal;
  font-style: normal;

  img {
    float: left;
    padding: 5px 30px;
  }
}

#main_menu {
  list-style-type: none;
  li {
    float: left;
    a {
      display: block;
      color: #2b2b2b;
      padding: 35px 10px;
      font-size: 25px;
      line-height: 25px;
      &:hover {
        color: #eb5252;
      }
    }
  }
}
